.heroContainer {
    background: linear-gradient(rgba(48, 44, 44, 0.5),rgba(51, 49, 49, 0.5)), url('../../images/bgfood.jpg');
    background-size: cover;
    height: 70vh;
}
.heroContainer > h1{
    color: #FF5B00;
    display: inline-block;
    margin-left: 30%;
    margin-top: 11%;
    font-size: 40px;
    font-family: 'Fredoka One', cursive;
}
.heroContainer h1 + button{
    background-color:  #990000;
    margin-left: 30px;
    padding: 10px;
    color: white
}
@media screen and (max-width: 760px){
    .heroContainer{
        background-size: cover;
        background-repeat: no-repeat;
        margin: auto;
        
    }
    .heroContainer button{
        display: block;
        position: relative;
        left: 28%;
        top: 10%;
        border-radius: 10px;
    }
}