.navList{
    display: flex;
    position: absolute;
    right: 40px;
    top: 5px;
    
    
}
.brand, .navList{
    padding-top: 30px;
    color: white;
}
.brandContainer{
    background-color: #990000;
    height: 25%;

}
.navList li{
    list-style: none;
    margin-right: 40px;
    
}
@media screen and (max-width: 560px){
    .navList{
        top: 10px;
    

    }
    .brand{
        font-size: 20px;
        margin-top: 10px;
        
    }
    .burger {
    
        
        
        display: none;
        
    }
}

@media screen and (max-width: 450px){
    .navList{
        flex-direction: column;
        background-color: Black;
        width: 100vw;
        height: 100%;
        margin: 40px auto;
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
        transition: all 0.5s ease-in;
        align-items: center;
        position: fixed;
        right: 0px;
        top: 40px;
        z-index: 50;

    }
    .open{
        -webkit-transform: translateX(0%);
                transform: translateX(0%);
    }
    .navList li{
        margin-top: 40px;
        color: white;
        text-align: center;
        
    }
    .burger{
        display: block;
        margin-bottom: 10px;
        padding-bottom: 30px;
        
    
    }
    .burger div{
        background-color: white;
        width: 25px;
        margin: 3px;
        height: 2px;
        cursor: pointer;
        margin-left: 84vw;
        
        
    }
    .brand{
        position: relative;
        top: 10px;
        /* background-color: aqua; */
        
    }
}

.heroContainer {
    background: linear-gradient(rgba(48, 44, 44, 0.5),rgba(51, 49, 49, 0.5)), url(/static/media/bgfood.0ebd098b.jpg);
    background-size: cover;
    height: 70vh;
}
.heroContainer > h1{
    color: #FF5B00;
    display: inline-block;
    margin-left: 30%;
    margin-top: 11%;
    font-size: 40px;
    font-family: 'Fredoka One', cursive;
}
.heroContainer h1 + button{
    background-color:  #990000;
    margin-left: 30px;
    padding: 10px;
    color: white
}
@media screen and (max-width: 760px){
    .heroContainer{
        background-size: cover;
        background-repeat: no-repeat;
        margin: auto;
        
    }
    .heroContainer button{
        display: block;
        position: relative;
        left: 28%;
        top: 10%;
        border-radius: 10px;
    }
}
img{
    width: 80%;
    object-fit: contain;
    display: inline-block;
    margin: 0 10%;
    
}
.menuWrapper{
    margin-top: 20px;
    background: #990000;
    border-radius: 15px;
    
}
.imgWrapper p{
    text-align: center;
    font-size: 20px;
    color: white;
}
.imgWrapper :nth-child(3){
    
   position: relative;
   top: 30px;
   right: 40px
}
.imgWrapper .menuLink{
    display: inline-block;
    border: 1px solid grey;
    padding: 3px;
    background-color:   #FF5B00;
    margin-left: 75%;
    border-radius: 5px;
    text-decoration: none;
    margin-top: 5px;
    margin-bottom: 5px;
   cursor: pointer;
    color: white;
}
.imgWrapper .menuLink:hover{
    background-color: rgb(223, 185, 185);
    color:#FF5B00
}
#desc{
    background-color:#FF5B00 ;
    width: 50%;
    box-shadow: 1px 1px rgb(196, 191, 191);
    border: 0.5px solid grey;
    border-radius: 6px;
    margin: auto;
    margin-top: 30px;
    margin-bottom: 15px
}
@media screen and (max-width: 760px){
    .imgWrapper .menuLink{
        margin-left: 40%;
        padding: 1.5px;
    }
    .imgWrapper :nth-child(3){
    
        position: relative;
        top: 0px;
        right: 0px
     }
}

.menuContainer{
    display: grid;
   grid-gap: 10px;
    grid-template-columns: repeat(3, 1fr);
    max-width: 95%;
    margin: 0 auto;
}
@media screen and (max-width: 760px){
    .menuContainer{
        grid-template-columns: 1fr;
    }
}
.adminWrapper{
    display: grid;
    grid-template-columns: 1fr 3fr;
}
.adminControl{
    border-right: 2px solid green;
    
}
.adminProfile img{
    padding: 10px;
    width: 18%;
    margin-left: 40%;
    border-radius: 50%;
    
}
.imgLabel{
    display: inline-block;
    margin-left: 30%;
    margin-bottom: 20px;
    font-size: 20px;
    color: red;
}
.addMenu{
    margin-top: 30px;
    
}
#logout{
    margin-left: 50%;
    cursor: pointer;
    background-color: #FF5B00;
    padding: 5px;
    border-radius: 10px;
    color: white;
}
.addMenu h4{
    text-align: center;
    font-family: 'Raleway', sans-serif;
}
.addMenu button{
  margin-left: 35%;
  background-color: green;
  color: white;
  padding: 5px;
  border-radius: 5px;
  cursor: pointer;
}
.addMenu input{
    display: block;
    margin: auto;
    margin-bottom: 10px;
    padding: 5px;

}
.orders{
    display: flex;
    flex-wrap: wrap;
    margin-top: 20px;
}
#h1{
    text-align: center;
    font-family: 'Raleway', sans-serif;
    margin-top: 30px;
    border-bottom: 2px solid green;
    display: inline-block;
    margin-left: 17%;
}
.adminEffect{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: repeat(10, 1fr);
    grid-gap: 10px;
}
.adminEffect .h3{
    text-align: center;
    height: 30px;
    margin-top: 30%;
    margin-left: 5px;
    color: white;
    font-size: 23px;
    font-family: 'Roboto Flex', sans-serif;
    background-color: green;
    border-radius: 6px;

    
    
}

@media screen and (max-width: 450px){
.adminWrapper{
    grid-template-columns: 1fr;
}
.adminEffect{
    grid-template-columns: 1fr;
}
}

.orderWrapper{
    width: 45%;
    border: 1px solid grey;
    box-shadow: 2px 2px rgb(78, 73, 73);
    color: grey;
    border-radius: 10px;
    margin-left: 2%;
    margin-right: 2%;
    margin-bottom: 10px;
    padding-left: 3px;
    text-align: center;
    
}
.orderWrapper p{
    
    margin-bottom: 10px;
    font-family: 'Roboto Flex', sans-serif;
}
.orderWrapper h4{
    
    margin-bottom: 10px;
    margin-top: 10px;
}
.orderWrapper button{
    padding: 3px;
    background-color: #FF5B00;
    color: white;
    border-radius: 5px;
    cursor: pointer;
}
@media screen and (max-width: 1060px){
    .orderWrapper{
        width: 90%;
        margin-left: 5%;
        margin-right: 5%;
    }
}

.adminMenuWrapper{
    margin-left: 10px;
    margin-top: 10px;
    border: 1px green solid;
    /* height: 200px; */
    box-shadow: 1px 1px grey;
    color: grey;
    text-align: center;
    border-radius: 10px;
}
.adminMenuWrapper *{
    margin-bottom: 10px;
    margin-top: 19px;
}
.adminWrapper .editButton{
    margin-left: 70%;
    color: white;
    background-color: green;
    padding: 2px;
    border-radius: 5px;
    cursor: pointer;
}
.adminMenuWrapper form > button{
    background: green;
    color: white;
    display: block;
    padding: 2px;
    border-radius: 5px;
    margin-left: 37%;
    margin-bottom: 15px;
    cursor: pointer;
}
.adminMenuWrapper .deletebutton{
 color: white;
 background-color:  #FF5B00;
 border-radius: 5px;
 padding: 10px;
 cursor: pointer;
}

.login{
    height: calc(100vh - 50px);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: linear-gradient(rgba(255, 255, 255, 0.5),rgba(255, 255, 255, 0.5)), url(/static/media/bgfood.0ebd098b.jpg);
    background-size: cover;

}
.loginForm{
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.loginForm > label{
    margin:10px 0px
}
.loginTitle{
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    font-size: 50px;

}
.loginInput{
    padding: 10px;
    background-color: white;
    border: none;
}
.loginButton{
    margin-top: 20px;
    cursor: pointer;
    background-color: lightcoral;
    border: none;
    color: white;
    border-radius: 10px;
    padding: 10px;

}
.loginButton:disabled{
    cursor: not-allowed;
    background-color: rgb(202, 151, 132);
}
.loginRegisterButton{
    position: absolute;
    top: 60px;
    right: 20px;
    background-color: teal;
    cursor: pointer;
    border: none;
    padding: 10px;
    color: white;
    border-radius: 10px;
}
.orderFormWrapper{
    background: linear-gradient(rgba(255, 255, 255, 0.5),rgba(255, 255, 255, 0.5)), url(/static/media/bgfood.0ebd098b.jpg);
    background-size: cover;
    background-repeat: no-repeat;
}
.orderFormWrapper input{
    display: block;
    margin-bottom: 30px;
    border: none;
    border-bottom: 1px solid grey;
    margin-top: 20px;
    padding: 10px;
}
.orderFormWrapper form, .orderFormWrapper h1{
    margin-left: 45%;
}
.orderFormWrapper label, .orderFormWrapper h1 {
    font-size: 25px;
    margin-bottom: 40px;
}
.orderFormWrapper label{
    font-family: 'Raleway', sans-serif;;
}
.orderFormWrapper h1{
    color: #990000;
    margin-top: 10px;
}
.orderFormWrapper button{
    padding: 10px;
    color: white;
    background-color: #990000;
    border-radius: 7px;
    cursor: pointer;
}
.orderFormWrapper select{
    display: block;
    margin-bottom: 30px;
    border: none;
    border-bottom: 1px solid grey;
    margin-top: 20px;
    padding: 10px;

}

@media screen and (max-width: 450px){
    .orderFormWrapper form, .orderFormWrapper h1{
        margin-left: 25%;
    }
}
.paystackWrapper input{
    display: block;
    margin-bottom: 30px;
    border: none;
    border-bottom: 1px solid grey;
    padding: 10px;
    margin-top: 10px;
}
.paystackWrapper label{
    color: white;
}
.paystackWrapper form{
    margin: 4% 40%;
    padding-top: 20px;
    padding-bottom: 20px;
    
}
.paystackWrapper{
background-color: #990000;
width: 50%;
margin: auto;
border-radius: 15px;
}
.paystackButton{
    padding: 11px;
    margin: 1% 40%;
    background-color: blue;
    color: white;
    cursor: pointer;
}
@media screen and (max-width:750px){
    .paystackWrapper{
        width: 100%;
