}
.successLink{
    display: inline-block;
    margin-top: 15px;
    margin-left: 5px;
    color: #990000;
    font-weight: bold
}

@media screen and(max-width: 750px){
    .successWrapper{
        width: 100vw;
        margin: 0px;
    }
    .successWrapper h1{
        padding-left: 0px;
        
    }
