img{
    width: 80%;
    object-fit: contain;
    display: inline-block;
    margin: 0 10%;
    
}
.menuWrapper{
    margin-top: 20px;
    background: #990000;
    border-radius: 15px;
    
}
.imgWrapper p{
    text-align: center;
    font-size: 20px;
    color: white;
}
.imgWrapper :nth-child(3){
    
   position: relative;
   top: 30px;
   right: 40px
}
.imgWrapper .menuLink{
    display: inline-block;
    border: 1px solid grey;
    padding: 3px;
    background-color:   #FF5B00;
    margin-left: 75%;
    border-radius: 5px;
    text-decoration: none;
    margin-top: 5px;
    margin-bottom: 5px;
   cursor: pointer;
    color: white;
}
.imgWrapper .menuLink:hover{
    background-color: rgb(223, 185, 185);
    color:#FF5B00
}
#desc{
    background-color:#FF5B00 ;
    width: 50%;
    box-shadow: 1px 1px rgb(196, 191, 191);
    border: 0.5px solid grey;
    border-radius: 6px;
    margin: auto;
    margin-top: 30px;
    margin-bottom: 15px
}
@media screen and (max-width: 760px){
    .imgWrapper .menuLink{
        margin-left: 40%;
        padding: 1.5px;
    }
    .imgWrapper :nth-child(3){
    
        position: relative;
        top: 0px;
        right: 0px
     }
}
